import { postHeaders, VERBS } from './constants'
import { myFetch } from './helpers'

export interface IMessageRequest {
  company: string
  contactPerson: string
  phone: string
  email: string
  request: string
  captcha: string
}

export const sendPromotionRequest = (promotion: IMessageRequest) => myFetch(
  '/json/v1/promotion/request',
  {
    method: VERBS.POST,
    body: promotion,
    headers: postHeaders,
  },
) as Promise<{ status: 'OK' }>

export const sendReptileDoc = (form: any) => myFetch(
  '/json/v1/form/reptile',
  {
    method: VERBS.POST,
    body: form,
    headers: postHeaders,
  },
) as Promise<{ file: string }>



