import { errorCatcher } from 'actions/message'
import { IAd } from 'api/interfaces'
import { sendReptileDoc } from 'api/misc'
import BaseField from 'components/utils/BaseField'
import Icon, { Loading } from 'components/utils/Icon'
import * as React from 'react'
import ReCAPTCHA from 'react-google-recaptcha'
import { actions, LocalForm } from 'react-redux-form'
import {
  Button, Checkbox, Container, Dropdown, Form, Header, Radio, Segment, TextArea,
} from 'semantic-ui-react'
const format = require('date-fns/format')
const parse = require('date-fns/parse')
import { Sex } from 'api/interfaces'
import { stripHTML } from 'utils/misc'
import { sexString } from 'utils/txt'
/* tslint:disable:max-line-length */

interface IState {
  form: { pristine: boolean, valid: boolean, value: IReptileDocForm }
  dispatch: any
  isPosting: boolean
  hasPosted: boolean
  file?: string
}

const reptileBornGroup: IGroup = () => ({
  reptile_born_at_current_owner: false,
  reptile_at_description: false,
  // reptile_born_at_orgini_country_by_others: false,
  // reptile_born_at_origin_country: false,
  reptile_born_at_previous_owner: false,
  reptile_born_at_unknown: false,
})

type IGroup = () => { [s: string]: boolean }

const initialForm = {
  attachment_cites: true,
  attachment_contract: true,
  attachment_others: true,
  attachment_picture: true,
  current_owner_address: '',
  current_owner_name: '',
  current_owner_phone: '',
  current_owner_post_area: '',
  place_date: 'Kristiansand, ' + format(new Date(), 'DD.MM.YYYY'),
  prev_owner_address: '',
  prev_owner_country: '',
  prev_owner_name: '',
  prev_owner_post_area: '',
  reptile_born_at_address: '',
  reptile_born_at_country: '',
  reptile_born_at_name: '',
  reptile_bort_at_postnumber: '',
  reptile_description: '',
  reptile_sex: '',
  reptile_species: '',
  reptile_species_scientific: '',
  reptile_year_of_birth: '',
  reptile_born_origin: '',
  reptile_born_other_imported: '',
  ...reptileBornGroup(),
}

interface SexOptions {
  key: Sex
  text: string
  value: Sex
}

const sexOptions: SexOptions[] = [Sex.m, Sex.f, Sex.o].map(
  (value) => ({ key: value, value, text: sexString(value) }),
)

type IReptileDocForm = typeof initialForm

const min = (val: number) => !!val

class ReptileDocForm extends React.Component<IConnectProps, IState> {
  public state = {
    isPosting: false,
    hasPosted: false,
    dispatch: (_e: any) => null,
    file: '',
    form: { pristine: true, valid: false, value: initialForm },
  }

  public componentDidMount() {
    if (!this.props.isFetching) {
      setTimeout(
        () => {
          this.props.getAds()
            .then(
              (ads) => {
                const ad = ads[Math.floor(Math.random() * (ads as IAd[]).length)] as IAd
                if (ad) {
                  const { categories } = this.props
                  const { sex, description_html, category: categoryId, date_of_birth } = ad
                  const category = categories[Number(categoryId)]
                  this.merge({
                    reptile_description: stripHTML(description_html),
                    reptile_sex: sex,
                    reptile_species: category.title,
                    reptile_species_scientific: category.title.split(' ').join('erius ') + 'as',
                    reptile_year_of_birth: parse(date_of_birth).getFullYear(),
                  })
                }
              })
        },
        2000,
      )
    }
  }

  public handleSubmit = (form: IReptileDocForm) => {
    const { dispatch } = this.state
    this.setState({ isPosting: true })
    sendReptileDoc(form)
      .then(
        ({ file }) => {
          this.setState({ isPosting: false, hasPosted: true })
          if (file) {
            dispatch(actions.reset('reptileDoc'))
            this.setState({file})
          }
        },
    )
      .catch((e: any) => {
        this.props.raiseError(e)
        this.setState({ isPosting: false })
      })
  }

  public handleCaptcha = (captcha: string) => {
    this.state.dispatch(actions.change('reptileDoc.captcha', captcha))
  }

  public MyRadio = ({ label, name, group }: { label: string, name: string, group: IGroup }) => {
    return (
      <Form.Field>
        <Radio
          label={label}
          name={name}
          checked={this.state.form.value[name]}
          onChange={() => this.handleRadioChange(name, group)}
        />
      </Form.Field>
    )
  }

  public handleRadioChange = (name: string, group: IGroup) => {
    const groupValues = group()
    groupValues[name] = true
    this.merge(groupValues)
  }
  public merge = (values: any) => this.state.dispatch(actions.merge('reptileDoc', values))

  public mergeCurrentOwner = (user: any) => {
    this.merge({
      current_owner_name: `${user.given_name} ${user.family_name}`,
      current_owner_address: `${user.address.address1}`,
      current_owner_post_area: `${user.address.post_code} ${user.address.post_area}`,
      current_owner_phone: `${user.mobile}`,
    })
  }

  public mergePrevOwner = (user: any) => {
    this.merge({
      prev_owner_name: `${user.given_name} ${user.family_name}`,
      prev_owner_address: `${user.address.address1}`,
      prev_owner_post_area: `${user.address.post_code} ${user.address.post_area}`,
      prev_owner_country: `${user.country || 'Norway'}`,
    })
  }

  public render() {
    const { isPosting } = this.state
    const { user, buyer } = this.props
    const { valid, pristine } = this.state.form
    const { MyRadio } = this
    const { reptile_at_description } = this.state.form.value as any
    if (!user || !buyer) {
      return null
    }
    return (
      <Container>
        <LocalForm
          className="ui form inverted"
          model="reptileDoc"
          onSubmit={this.handleSubmit}
          getDispatch={(dispatch) => this.setState({ dispatch })}
          onUpdate={({ $form }) => this.setState({ form: $form })}
          validators={{
            // Field-level validators
            captcha: { min },
          }}
          initialState={initialForm}
        >
          <Segment inverted={true}>
            <Header inverted={true} className="header">
              Dyreholder
              <Button.Group
                size="small"
                compact={true}
                primary={true}
                floated="right"
              >
                <Button onClick={() => this.mergeCurrentOwner(user)} >
                  Bruk min informasjon
                </Button>
                <Button onClick={() => this.mergeCurrentOwner(buyer)} >
                  Bruk kjøpers informasjon
                </Button>
              </Button.Group>
            </Header>
            <Form.Group widths="equal">
              <BaseField
                required={true}
                label="Navn på perosn eller virksomhet"
                model={'reptileDoc.current_owner_name'}
                maxLength={80}
              />
              <BaseField
                required={true}
                label="Adresse"
                model={'reptileDoc.current_owner_address'}
                maxLength={80}
              />
            </Form.Group>
            <Form.Group widths="equal">
              <BaseField
                required={true}
                label="Postnummer og -sted"
                model={'reptileDoc.current_owner_post_area'}
                maxLength={80}
              />
              <BaseField
                required={true}
                label="Telefonnummer"
                model={'reptileDoc.current_owner_phone'}
                maxLength={80}
              />
            </Form.Group>

          </Segment>
          <Segment inverted={true}>
            <Header inverted={true}>
              Forrige dyreholder
              <Button.Group
                size="small"
                compact={true}
                primary={true}
                floated="right"
              >
                <Button onClick={() => this.mergePrevOwner(user)} >
                  Bruk min informasjon
                </Button>
                <Button onClick={() => this.mergePrevOwner(buyer)} >
                  Bruk kjøpers informasjon
                </Button>
              </Button.Group>
            </Header>
            <Form.Group widths="equal">
              <BaseField
                required={true}
                label="Navn på person eller virksomhet"
                model={'reptileDoc.prev_owner_name'}
                maxLength={80}
              />
              <BaseField
                required={true}
                label="Address"
                model={'reptileDoc.prev_owner_address'}
                maxLength={80}
              />
            </Form.Group>
            <Form.Group widths="equal">
              <BaseField
                required={true}
                label="Postnummer og -sted"
                model={'reptileDoc.prev_owner_post_area'}
                maxLength={80}
              />
              <BaseField
                required={true}
                label="Land"
                model={'reptileDoc.prev_owner_country'}
                maxLength={80}
              />
            </Form.Group>

          </Segment>

          <Segment inverted={true}>
            <Header inverted={true} className="header">Opphav</Header>
            <MyRadio
              label="Dyret og foreldrene til dyret er født hos dyreholderen"
              name="reptile_born_at_current_owner"
              group={reptileBornGroup}
            />
            <MyRadio
              label="Dyret og foreldrene til dyret er født hos den forrige dyreholderen."
              name={'reptile_born_at_previous_owner'}
              group={reptileBornGroup}
            />
            <MyRadio
              label="Beskriv med adresse"
              name={'reptile_at_description'}
              group={reptileBornGroup}
            />
            {reptile_at_description && (
              <Segment inverted={true}>
                <Form.Group widths="equal">
                  <BaseField
                    required={false}
                    label="Navn på perosn eller virksomhet"
                    model={'reptileDoc.reptile_born_at_name'}
                    maxLength={80}
                  />
                  <BaseField
                    required={false}
                    label="Postnummer og -sted"
                    model={'reptileDoc.reptile_born_at_address'}
                    maxLength={80}
                  />
                </Form.Group>
                <Form.Group widths="equal">
                  <BaseField
                    required={false}
                    label="Gate"
                    model={'reptileDoc.reptile_bort_at_postnumber'}
                    maxLength={80}
                  />
                  <BaseField
                    required={false}
                    label="Land"
                    model={'reptileDoc.reptile_born_at_country'}
                    maxLength={80}
                  />
                </Form.Group>
              </Segment>
            )}
            <BaseField
              required={false}
              label="Dyreholderen har opprinnelig innført dyret til Norge fra"
              model={'reptileDoc.reptile_born_origin'}
              maxLength={80}
            />
            <BaseField
              required={false}
              label="Andre enn dyreholderen har opprinnelig innført dyret til Norge fra"
              model={'reptileDoc.reptile_born_other_imported'}
              maxLength={80}
            />
            <MyRadio
              label="Ukjent."
              name={'reptile_born_at_unknown'}
              group={reptileBornGroup}
            />

          </Segment>

          <Segment inverted={true}>
            <Header inverted={true}>Om dyret</Header>
            <BaseField
              required={true}
              label="Størrelse, farge, mønster og andre spesielle kjennetegn"
              typeComponent={TextArea}
              model={'reptileDoc.reptile_description'}
              maxLength={2000}
            />
            <BaseField
              required={true}
              label="Kjønn"
              typeComponent={Dropdown}
              model={'reptileDoc.reptile_sex'}
              controlProps={{
                options: sexOptions,
                selection: true,
                onChange: (_e: any, d: any) => this.state.dispatch(actions.change('reptileDoc.reptile_sex', d.value)),
              }}
            />
            <BaseField
              required={true}
              label="Norsk artsnavn"
              model={'reptileDoc.reptile_species'}
              maxLength={80}
            />
            <BaseField
              required={true}
              label="Vitenskapelig artsnavn"
              model={'reptileDoc.reptile_species_scientific'}
              maxLength={80}
            />
            <BaseField
              required={true}
              label="Fødselsår"
              model={'reptileDoc.reptile_year_of_birth'}
              maxLength={80}
            />
            <BaseField
              required={true}
              label="Sted og dato"
              model={'reptileDoc.place_date'}
              maxLength={80}
            />
          </Segment>

          <Segment inverted={true}>

            <Header inverted={true} className="header">Vedlegg</Header>

            <BaseField
              required={true}
              label="kopi av CITES-tillatelse"
              typeComponent={Checkbox}
              model={'reptileDoc.attachment_cites'}
            />
            <BaseField
              required={true}
              label="kontrakt eller attest som viser hvem dyreholderen eventuelt har kjøpt dyret av"
              typeComponent={Checkbox}
              model={'reptileDoc.attachment_contract'}
            />

            <BaseField
              required={true}
              label="andre dokumenter om dyrets art og opprinnelse"
              typeComponent={Checkbox}
              model={'reptileDoc.attachment_others'}
            />
            <BaseField
              required={true}
              label="Et fargebilde av dyret i A4-format som viser viktige kjennetegn på at dyret tilhører den lovlige arten"
              typeComponent={Checkbox}
              model={'reptileDoc.attachment_picture'}
            />
          </Segment>
          <Button
            icon={true}
            primary={true}
            disabled={!valid || isPosting}
            type="submit"
          >
            {(isPosting) ? (
              <Loading active={true} />
            ) : (
                <Icon icon="envelope" />
              )}
            Send inn forespørsel
          </Button>
        </LocalForm>
        {this.state.file && (
          <Button
            href={this.state.file}
          >
            Last ned fil
          </Button>
        )}
        {
          !pristine && (
            <ReCAPTCHA
              sitekey={process.env.REACT_APP_GOOGLE_RECAPTCHA_SITE_KEY}
              onChange={this.handleCaptcha}
              theme="dark"
              badge="bottomright"
            />
          )
        }
      </Container >
    )
  }
}

import { getAds } from 'actions/ad'
import { connect, Dispatch } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ user, ads, base }: IStoreState) => {
    const buyer = {
      given_name: 'Ola',
      family_name: 'Johansen',
      mobile: 12341234,
      address: {
        address1: 'Kjeppeveien 4',
        address2: 'C/O ...',
        post_code: '4633',
        post_area: 'Kristiansand',
      },
    }
    return {
      categories: base.categories,
      user: user.user,
      buyer,
      isFetching: ads.isFetching,
    }
  },
  (dispatch: Dispatch<{}>) => {
    return {
      raiseError: errorCatcher(dispatch, 'reptileDocRequest'),
      getAds: () => dispatch(getAds()),
    }
  },
)

type IConnectProps = typeof connectCreator.allProps
export default connectCreator(ReptileDocForm)



